
//    seo-custom   //

.seo-custom {
  padding-top: 110px;
  padding-bottom: 136px;
  @media (max-width: 1023.98px) {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @media (max-width: 767.98px) {
    padding-top: 56px;
    padding-bottom: 56px;
  }

  &__photo-mobile {
    display: none;
    @media (max-width: 1023.98px) {
      display: block;
    }
    @media (max-width: 480.98px) {
      max-width: 230px;
      text-align: center;
      margin: 0 auto;
    }
  }

  .main-title .title p {
    letter-spacing: 0;
  }

  &__content {
    display: inline-flex;
  }

  &__photo-block {
    width: 387px;
    position: relative;
    @media (max-width: 1023.98px) {
      display: none;
    }
  }

  &__photo {
    position: absolute;
    width: 513px;
    height: 468px;
    top: -41px;
    left: -181px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: 1440.98px) {
      width: 430px;
      height: auto;
      left: -80px;
    }
  }

  &__text {
    width: 750px;
    @media (max-width: 1199.98px) {
      width: 600px;
    }
    @media (max-width: 1023.98px) {
      width: 100%;
    }
  }

  .typography {
    margin-top: 23px;
  }
}


//   seo-custom    //